import React from 'react';
import Particles from 'react-particles-js';

export default class ParticleWave extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Particles
        params={{
          particles: {
            line_linked: {
              shadow: {
                enable: true,
                color: '#199bfc',
                blur: 5
              }
            }
          }
        }}
        style={{
          position: 'absolute',
          width: '100%'
        }}
      />
    );
  }
}
